import { 
  MapContainer,
  TileLayer,
  ZoomControl,
  GeoJSON
} from 'react-leaflet';
import * as L from 'leaflet';
import {
  LeafletMouseEvent
} from 'leaflet';
import tinycolor from 'tinycolor2';
import 'leaflet/dist/leaflet.css';

import { maxCrimeRank } from '../../Constants';
import { isMobileWidth } from '../../Utils';

import ICrimeData from '../../interfaces/ICrimeData';
import ISuburbFeature from '../../interfaces/ISuburbFeature';

import Attribution from '../Attribution/Attribution';

import './Map.css';

interface IMapProps {
  center: [number, number],
  zoom: number,
  crimeData: ICrimeData | null,
  selectedSuburb: string,
  onMapReady: (mapObject: L.Map) => void,
  onSuburbOver: (id: number) => void,
  onSuburbOut: (id: number) => void,
  onSuburbClick: (id: number) => void,
  onSuburbDblClick: (id: number) => void,
}

const tStartColor = tinycolor('green');

function getSuburbColor(crimeRank: number, medianRank: number) {
  let mult = 1;
  if(crimeRank <= medianRank) {
    mult = 0.5 * (crimeRank / medianRank);
  } else {
    mult = 0.5 + 0.5 * (crimeRank / maxCrimeRank);
  }
  const color = tStartColor.clone().spin(-120 * mult);
  return color.toHexString();
}

export default function Map(props: IMapProps) {
  function styleSuburb(suburb: ISuburbFeature | undefined) {
    if(props.crimeData === null) return {};
    if(suburb === undefined) return {};
    return {
      stroke: false,
      fillOpacity: 0.6,
      color: getSuburbColor(suburb.properties.crime_rank, props.crimeData.properties.median_rank)
    };
  }

  function mouseOverHandler(event: LeafletMouseEvent, highlight = true) {
    const layer = event.layer;
    const suburb = event.layer.feature as ISuburbFeature;
    if(suburb.id !== undefined) props.onSuburbOver(Number(suburb.id));

    if(highlight) {
      layer.setStyle({
        stroke: true
      });
      if(!L.Browser.ie && !L.Browser.opera && !L.Browser.edge) {
        layer.bringToFront();
      }
    }
  }

  function mouseOutHandler(event: LeafletMouseEvent) {
    const layer = event.layer;
    const suburb = event.layer.feature as ISuburbFeature;
    if(suburb.id !== undefined) props.onSuburbOut(Number(suburb.id));

    layer.setStyle({
      stroke: false
    });
  }

  function mouseClickHandler(event: LeafletMouseEvent) {
    if(isMobileWidth()) {
      mouseOverHandler(event, false);
      return;
    }
    const suburb = event.layer.feature as ISuburbFeature;
    if(suburb.id !== undefined) props.onSuburbClick(Number(suburb.id));
  }

  function dblMouseClickHandler(event: LeafletMouseEvent) {
    const suburb = event.layer.feature as ISuburbFeature;
    if(suburb.id !== undefined) props.onSuburbDblClick(Number(suburb.id));
  }
  
  // function renderSuburbs() {
  //   const suburbs = props.suburbsData === null ? [] : Object.values(props.suburbsData.suburbs);
  //   const medianRank = props.suburbsData === null ? 0 : props.suburbsData.median_ratio;
  //   return suburbs.map((s, idx) => <SuburbPolygon key={idx} suburb={s} medianRank={medianRank} selected={s.name === props.selectedSuburb} />);
  // }

  return (
  <MapContainer className="Map" 
    center={props.center}
    zoom={props.zoom}
    zoomControl={false}
    attributionControl={false}
    whenCreated={props.onMapReady}
  >
    <TileLayer
      attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a>'
      url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
    />

    {props.crimeData !== null ? 
      <GeoJSON 
        data={props.crimeData} 
        style={styleSuburb}
        eventHandlers={{
          click: mouseClickHandler,
          dblclick: dblMouseClickHandler,
          mouseover: mouseOverHandler,
          mouseout: mouseOutHandler,
        }}
      /> 
    : null}

    <ZoomControl position={'bottomright'} />
    <Attribution /> 
  </MapContainer>
  );
}
