import {
  useState
} from 'react';
import {
  NavLink
} from 'react-router-dom';

import Logo from '../../components/Logo/Logo'

import { ReactComponent as MenuIcon } from '../../images/menu-icon.svg';
import { ReactComponent as CloseIcon } from '../../images/x-icon.svg';

import './Sidebar.css';

export default function Sidebar() {
  const [open, setOpen] = useState<boolean>(false);

  return (
    <div className={"Sidebar__container " + (open ? 'Sidebar__container--active': '')}>
      <button className="Sidebar__open-trigger" type="button" onClick={() => setOpen(true)}>
        <MenuIcon />
      </button>
      <div className="Sidebar__close-trigger" onClick={() => setOpen(false)}></div>
      <div className="Sidebar">
        <div className="Sidebar__header">
          <Logo />

          <button className="Sidebar__close-btn" type="button" onClick={() => setOpen(false)}>
            <CloseIcon />
          </button>
        </div>
        
        <div className="Sidebar__content">
          <nav className="Sidebar__menu">
            <NavLink to="/" className={({ isActive }) => 'Sidebar__menu-item ' + (isActive ? 'Sidebar__menu-item--active' : '')} onClick={() => setOpen(false)}>Map</NavLink>
            <NavLink to="suburbs" className={({ isActive }) => 'Sidebar__menu-item ' + (isActive ? 'Sidebar__menu-item--active' : '')} onClick={() => setOpen(false)}>Suburbs</NavLink>
            <NavLink to="faq" className={({ isActive }) => 'Sidebar__menu-item ' + (isActive ? 'Sidebar__menu-item--active' : '')} onClick={() => setOpen(false)}>FAQ</NavLink>
          </nav>
        </div>
      </div>
    </div>
  );
}
