import * as L from 'leaflet';
import { 
  MapContainer,
  MapConsumer,
  TileLayer,
  Polygon as LeafletPolygon,
} from 'react-leaflet';
import { Polygon } from 'geojson';
import { 
  Link,
  useParams
} from 'react-router-dom';
import { Helmet } from 'react-helmet-async';

import { maxCrimeRank } from '../../Constants';
import { 
  getSuburbMapLink,
  getSuburbByName,
  getSuburbProfileLink
} from '../../Utils';

import ISuburbFeature from '../../interfaces/ISuburbFeature';
import IBorderingSuburbs from '../../interfaces/IBorderingSuburbs';

import ContentPageHeader from '../../components/ContentPageHeader/ContentPageHeader';
import ContentPageFooter from '../../components/ContentPageFooter/ContentPageFooter';

import './SuburbProfilePage.css';

export default function SuburbProfilePage(props: {
  suburbs: ISuburbFeature[] | null
  borderingSuburbs: IBorderingSuburbs | null
}) {
  //const params: ISuburbProfilePageParams = useParams();
  const { key } = useParams();
  const suburb = props.suburbs === null ? null : props.suburbs.find(s => s.properties.key === key);
  if(suburb === undefined || suburb === null) return (
    <div className="SuburbProfilePage Page">
      <ContentPageHeader />
      <section className="Page__content">
        <p>Loading suburb data...</p>
      </section>
    </div>
  );

  const crime = suburb.properties.crime;
  const centerPoint = (
    L.bounds(
      L.point(suburb.properties.bounds[1], suburb.properties.bounds[0]),
      L.point(suburb.properties.bounds[3], suburb.properties.bounds[2])
    ).getCenter()
  );
  const center = L.latLng(centerPoint.x, centerPoint.y);

  return (
    <div className="SuburbProfilePage Page">
      <Helmet>
        <title>Crime rate in {suburb.properties.name} QLD, Australia - RedSuburbs</title>
        <meta name="description" content={`Crime rate profile of ${suburb.properties.name} suburb`} />

        <meta property="og:title" content={`Crime rate in ${suburb.properties.name}`} />
        <meta property="og:url" content={`https://redsuburbs.com.au/suburbs/${suburb.properties.key}`} />
        <meta property="og:description" content={`Crime rate profile of ${suburb.properties.name} suburb`} />

        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:description" content={`Crime rate profile of ${suburb.properties.name} suburb`} />
      </Helmet>

      <ContentPageHeader />
      <section className="Page__content">
        <h1 className="Page__title">Crime rate in {suburb.properties.name} QLD, Australia</h1>
        <div className="SuburbProfilePage__population">Population: {suburb.properties.population}</div>
        <div className="SuburbProfilePage__total-crimes">Total crimes: {crime['total']}</div>
        <h2 className="Page__sub-title">Crime Rank: {suburb.properties.crime_rank.toFixed(0)}/{maxCrimeRank}</h2>

        <div className="SuburbProfilePage__crime-container grid">
          <div className="grid__col--span6">
            <h4 className="SuburbProfilePage__sub-title">Top crime types:</h4>
            <div className="SuburbProfilePage__crimes">
            {Object.keys(crime)
              .filter(c => c !== 'total')
              .sort((a, b) => crime[b] - crime[a])
              .map(crime_type => (
              <div key={crime_type} className="SuburbProfilePage__crime">
                <div className="SuburbProfilePage__crime-name">{crime_type}</div>
                <div className="SuburbProfilePage__crime-val">{crime[crime_type]}</div>
              </div>))}
            </div>
          </div>
          <div className="grid__col--span6">
            <MapContainer className="SuburbProfilePage__map" 
              center={center}
              zoom={14}
              zoomControl={false}
              attributionControl={true}
            >
              <TileLayer
                attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a>'
                url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
              />
              <LeafletPolygon 
                positions={(suburb.geometry as Polygon).coordinates[0].map(p => L.latLng(p[1], p[0]))}
                pathOptions={{ color: '#e84917' }}
              ></LeafletPolygon>
              <MapConsumer>
              {(map) => {
                map.setView(center, 14);
                return null;
              }}
              </MapConsumer>
            </MapContainer>

            <h4 className="SuburbProfilePage__sub-title">Neighbouring Suburbs:</h4>
            <div className="SuburbProfilePage__bordering-suburbs">
            {props.borderingSuburbs === null ? null : props.borderingSuburbs[suburb.properties.name].map(suburbName => {
              const suburb = getSuburbByName(suburbName, props.suburbs);
              if(suburb === null)
                return (<span key={suburbName} className="SuburbProfilePage__bordering-suburb">{suburbName}</span>);
              else
                return (
                  <Link key={suburbName} className="SuburbProfilePage__bordering-suburb" to={getSuburbProfileLink(suburb)}>
                    {suburbName} ({suburb.properties.crime_rank.toFixed(0)})
                  </Link>
                );
            })}
            </div>
          </div>
        </div>

        <Link className="btn btn--inline" to="/suburbs">Suburbs list</Link>&nbsp;
        <Link className="btn btn--red btn--inline" to={getSuburbMapLink(suburb)}>See on Crime Map</Link>
      </section>
      <ContentPageFooter />
    </div>
  );
}
