import React, {
  useState
} from 'react';
import * as L from 'leaflet';

import { ReactComponent as SearchIcon } from '../../images/search-icon.svg';

import ISuburbFeature from '../../interfaces/ISuburbFeature'; 

import './SuburbSearch.css';

interface ISuburbSearchProps {
  suburbs: ISuburbFeature[]
  mapObject: L.Map | null,
  onSuburbSelected: (suburbName: string) => void
}

export default function SuburbSearch(props: ISuburbSearchProps) {
  const [resultsVisible, setResultsVisible] = useState<boolean>(false);
  const [searchQuery, setSearchQuery] = useState<string>('');

  function suburbClickHandler(suburb: ISuburbFeature) {
    const suburbName = suburb.properties.name;
    const bounds = suburb.properties.bounds;
    setResultsVisible(false);
    setSearchQuery(suburbName);
    props.onSuburbSelected(suburbName);

    if(props.mapObject !== null) {
      props.mapObject.fitBounds([
        [bounds[1], bounds[0]],
        [bounds[3], bounds[2]]
      ]);
    }
  }

  function submitHandler(event: React.FormEvent<HTMLFormElement>) {
    event.preventDefault();
    const searchField = (event.target as HTMLElement).querySelector('.SuburbSearch__search');
    if(searchField === null) return;
    const topResult = (event.target as HTMLElement).querySelector('.SuburbSearch__result');
    if(topResult === null) return;
    (topResult as HTMLElement).click();
    (searchField as HTMLElement).blur();
  }

  function renderSearchResults(suburbs: ISuburbFeature[], searchQuery: string) {
    let arr = [...suburbs];
    if(searchQuery !== '') {
      arr = arr.filter(s => s.properties.name.toLowerCase().indexOf(searchQuery.toLowerCase()) !== -1);
    }
    arr = arr.sort((a, b) => b.properties.population - a.properties.population);
    arr = arr.slice(0, 20);

    return arr.map(s => <button key={s.properties.name} className="SuburbSearch__result" type="button" onClick={() => suburbClickHandler(s)}>{s.properties.name}</button>);
  }

  return (
    <div className="SubburbSearch__container">
      {resultsVisible ? <div className="SuburbSearch__close-trigger" onClick={() => setResultsVisible(false)}></div> : null}
      <form className="SuburbSearch" 
        onFocus={() => setResultsVisible(true)} 
        onBlur={() => {/*setResultsVisible(false)*/}}
        onSubmit={submitHandler}
      >
        <div className="SuburbSearch__search-container">
          <input 
            className="SuburbSearch__search input" 
            name="search"
            type="text" 
            placeholder="Search Red Suburbs"
            autoComplete="off" 
            value={searchQuery}
            
            onInput={(e: React.ChangeEvent<HTMLInputElement>) => setSearchQuery(e.target.value)}
          />
          <button className="SuburbSearch__button" type="submit">
            <SearchIcon />
          </button>
        </div>
        <div className={"SuburbSearch__results " + (resultsVisible ? 'SuburbSearch__results--visible' : '') }>
          {renderSearchResults(props.suburbs, searchQuery)}
        </div>
      </form>
    </div>
  )
}
