import React, {
  useState,
  useEffect,
  useCallback,
  useMemo
} from 'react';

import {
  useLocation,
  useNavigate
} from 'react-router-dom';

import { getSuburbProfileLink } from '../../Utils';

import Map from '../../components/Map/Map';
import Feedback from '../../components/Feedback/Feedback';
import SuburbQuickView from '../../components/SuburbQuickView/SuburbQuickView';
import SuburbSearch from '../../components/SuburbSearch/SuburbSearch';

import ICrimeData from '../../interfaces/ICrimeData';
import ISuburbFeature from '../../interfaces/ISuburbFeature';

import './HomePage.css';

function useQuery() {
  return new URLSearchParams(useLocation().search);
}

export default function HomePage(props: {
  crimeData: ICrimeData | null
}) {
  const navigate = useNavigate();
  const [overSuburb, setOverSuburb] = useState<number | null>(null);
  const [mapObject, setMapObject] = useState<L.Map | null>(null);

  const query = useQuery();
  let latlng: [number, number] = [-27.4594, 153.0289];
  let zoom = 12;
  if(query.has('lat') && query.has('lng')) latlng = [Number(query.get('lat')), Number(query.get('lng'))];
  if(query.has('zoom')) zoom = Number(query.get('zoom'));

  
  useEffect(() => {
    if(mapObject === null) return;

    let mapChangeTimeout: ReturnType<typeof setTimeout> | null = null;
    function handleMapChange() {
      if(mapObject === null) return;

      if(mapChangeTimeout !== null) clearTimeout(mapChangeTimeout);

      mapChangeTimeout = setTimeout(function() {
        const center = mapObject.getCenter();
        const zoom = mapObject.getZoom();
        navigate(`/?lat=${center.lat}&lng=${center.lng}&zoom=${zoom}`);
      }, 500);
    }

    mapObject.on('moveend', () => handleMapChange());
    mapObject.on('zoomend', () => handleMapChange());
  }, [mapObject, navigate])

  const getOverSuburb = useCallback(function (crimeData: ICrimeData | null, overSuburb: number | null) {
    if(crimeData === null) return null;
    if(overSuburb === null) return null;
    const suburb = crimeData.features[overSuburb];
    return suburb as ISuburbFeature;
  }, []);

  const suburbMapClickHandler = useCallback(function(crimeData: ICrimeData | null, suburbId: number) {
    if(crimeData === null) return;
    const suburb = crimeData.features[suburbId];
    if(suburb === null) return; 
    //console.log(suburb);
    navigate(getSuburbProfileLink(suburb as ISuburbFeature));
  }, [navigate]);

  const crimeMap = useMemo(() => <Map 
    center={latlng}
    zoom={zoom}
    crimeData={props.crimeData} 
    selectedSuburb={''} 
    onMapReady={map => setMapObject(map)}
    onSuburbOver={s => setOverSuburb(s)} 
    onSuburbOut={s => setOverSuburb(null)}
    onSuburbClick={s => suburbMapClickHandler(props.crimeData, s)}
    onSuburbDblClick={s => suburbMapClickHandler(props.crimeData, s)}
  />, [props.crimeData]);

  return (
    <div className="HomePage Page Page--no-top-space">
      {crimeMap} 
      <SuburbQuickView suburb={getOverSuburb(props.crimeData, overSuburb)} />
      <SuburbSearch suburbs={props.crimeData === null ? [] : (props.crimeData.features as ISuburbFeature[])} mapObject={mapObject} onSuburbSelected={s => console.log(s)} />
      <Feedback />
    </div>
  );
}
