import { maxCrimeRank } from '../../Constants';

import ISuburbFeature from '../../interfaces/ISuburbFeature';

import './SuburbQuickView.css';

interface ISuburbQuickViewProps {
  suburb: ISuburbFeature | null
}

export default function SuburbQuickView(props: ISuburbQuickViewProps) {
  return (
    <div className="SuburbQuickView__container">
    {(function() {
      if(props.suburb === undefined || props.suburb === null) return null;
      const info = props.suburb.properties;
      const crime = info.crime;

      return (
        <div className={"SuburbQuickView SuburbQuickView--visible"}>
          <div className="SuburbQuickView__meta-container">
            <div className="SuburbQuickView__crime-rank SuburbQuickView__stat">Crime Rank: {info.crime_rank.toFixed(0)}/{maxCrimeRank}</div>
            <h3 className="SuburbQuickView__title">{info.name}</h3>

            <div className="SuburbQuickView__stat">Population: {info.population}</div>
            <div className="SuburbQuickView__stat">Total crimes: {crime['total']}</div>
          </div>

          <div className="SuburbQuickView__crime-container">
            <h4 className="SuburbQuickView__sub-title">Top crime types:</h4>
            <div className="SuburbQuickView__crimes">
            {Object.keys(crime)
              .filter(c => c !== 'total')
              .sort((a, b) => crime[b] - crime[a])
              .slice(0, 5)
              .map(crime_type => (
              <div key={crime_type} className="SuburbQuickView__crime">
                <div className="SuburbQuickView__crime-name">{crime_type}</div>
                <div className="SuburbQuickView__crime-val">{crime[crime_type]}</div>
              </div>))}
            </div>
          </div>

          <div className="SuburbQuickView__profile-notice">
            Double tap a suburb to open full profile
          </div>
        </div>
      );
    })()}
    </div>
  )
}
