import React, {
  useState,
  useEffect,
} from 'react';
import { Helmet } from 'react-helmet-async';

import { 
  Routes,
  Route,
  Navigate
} from 'react-router-dom';

import './styles/colors.css';
import './styles/typography.css';

import './styles/components/grid-system.css';

import './styles/elements/buttons.css';
import './styles/elements/inputs.css';

import Sidebar from './components/Sidebar/Sidebar';

import HomePage from './pages/HomePage/HomePage';
import NotFoundPage from './pages/NotFoundPage/NotFoundPage';
import SuburbsPage from './pages/SuburbsPage/SuburbsPage';
import SuburbProfilePage from './pages/SuburbProfilePage/SuburbProfilePage';
import FAQPage from './pages/FAQPage/FAQPage';

import ICrimeData from './interfaces/ICrimeData';
import ISuburbFeature from './interfaces/ISuburbFeature';
import IBorderingSuburbs from './interfaces/IBorderingSuburbs';

import './App.css';

function App() {
  const [crimeData, setCrimeData] = useState<ICrimeData | null>(null);
  const [borderingSuburbs, setBorderingSuburbs] = useState<IBorderingSuburbs | null>(null);

  useEffect(() => {
    (async () => {
      const response = await fetch('/data/suburbs-data.geo.json');
      if(!response.ok) {
        alert('Failed to load suburbs data!');
        throw new Error('Failed to load suburbs data!');
      }

      const data: ICrimeData = await response.json();
      setCrimeData(data);
    })();
    (async () => {
      const response = await fetch('/data/bordering-suburbs.json');
      if(!response.ok) {
        alert('Can\'t load list of bordering suburbs!');
        throw new Error('Can\'t load list of bordering suburbs!');
      }

      const data: IBorderingSuburbs = await response.json();
      setBorderingSuburbs(data);
    })();
  }, []);

  return (
    <div className="App">
      <Helmet>
        <title>Crime Map QLD online - RedSuburbs</title>
        <meta name="description" content="Explore crime map of QLD based on QPS data and easy to use." />

        <meta property="og:locale" content="en_AU" />
        <meta property="og:type" content="website" />
        <meta property="og:site_name" content="RedSuburbs" />
        <meta property="og:title" content="Crime Map QLD" />
        <meta property="og:description" content="Explore online crime map of QLD based on QPS data" />
        <meta property="og:url" content="https://redsuburbs.com.au" />
        <meta property="og:image:width" content="1200" />
        <meta property="og:image:height" content="630" />
        <meta property="og:image"
              content="https://redsuburbs.com.au/images/redsuburbs-image.jpg" />

        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:description" content="Explore online crime map of QLD based on QPS data" />
      </Helmet>

      <Routes>
        <Route path="/" element={<HomePage crimeData={crimeData} />} />
        <Route path="/map" element={<Navigate replace to="/" />} />
        <Route path="/suburbs" element={<SuburbsPage suburbs={crimeData == null ? [] : (crimeData.features as ISuburbFeature[])} />} />
        <Route path="/suburbs/:key" element={<SuburbProfilePage borderingSuburbs={borderingSuburbs} suburbs={crimeData == null ? [] : (crimeData.features as ISuburbFeature[])} />} />
        <Route path="/faq" element={<FAQPage />} />
        <Route path="*" element={<NotFoundPage />} />
      </Routes>
      <Sidebar />
    </div>
  );
}

export default App;
