import { Link } from 'react-router-dom';
import { Helmet } from 'react-helmet-async';

import { 
  getSuburbMapLink,
  getSuburbProfileLink
} from '../../Utils';

import ContentPageHeader from '../../components/ContentPageHeader/ContentPageHeader';
import ContentPageFooter from '../../components/ContentPageFooter/ContentPageFooter';

import ISuburbFeature from '../../interfaces/ISuburbFeature';

import './SuburbsPage.css';

function sortSuburbs(s1: ISuburbFeature, s2: ISuburbFeature) {
  const t1 = s1.properties.name.toUpperCase();
  const t2 = s2.properties.name.toUpperCase();
  return (t1 < t2) ? -1 : (t1 > t2) ? 1 : 0;
}

export default function SuburbsPage(props: {
  suburbs: ISuburbFeature[]
}) {
  return (
    <div className="SuburbsPage Page">
      <Helmet>
        <title>QLD Suburbs List - RedSuburbs</title>
        <meta name="description" content="QLD Suburbs list and their crime rate" />

        <meta property="og:title" content="QLD Suburbs List" />
        <meta property="og:url" content="https://redsuburbs.com.au/suburbs" />
        <meta property="og:description" content="QLD Suburbs list and their crime rate" />

        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:description" content="QLD Suburbs list and their crime rate" />
      </Helmet>

      <ContentPageHeader />
      <section className="Page__content">
        <h1 className="Page__title">QLD Suburbs List</h1>

        <table className="SuburbsPage__suburbs">
          <thead>
            <tr>
              <th className="SuburbsPage__crime-rank-header">Crime Rank</th>
              <th className="SuburbsPage__suburb-name-header">Suburb Name</th>
              <th className="SuburbsPage__actions-header">Actions</th>
            </tr>
          </thead>
          <tbody>
            {[...props.suburbs].sort(sortSuburbs).map(s => (
              <tr key={s.properties.key} className="SuburbsPage__suburb">
                <td className="SuburbsPage__suburb-rank">{s.properties.crime_rank.toFixed(0)}</td>
                <td><Link className="SuburbsPage__suburb-name" to={getSuburbProfileLink(s)}>{s.properties.name}</Link></td>
                <td><Link className="SuburbsPage__suburb-btn btn btn--inline btn--red" to={getSuburbMapLink(s)}>On Map</Link></td>
              </tr>
            ))}
          </tbody>
        </table>

        <p>
          <Link className="btn btn--red btn--inline" to="/">Back to Crime Map</Link>
        </p>
      </section>
      <ContentPageFooter />
    </div>
  );
}

